<template>
  <div class="homepage">
    <div class="slide-view">
      <div
        class="slide-child"
        v-for="(item, index) in componentList"
        :key="index"
      >
        <div class="item-head">
          <span>{{ item.title }}</span>
          <img
            @click="arrowTap(index)"
            :class="['pointer', item.open ? '' : 'transform180']"
            src="@/assets/icon/arrow-up.png"
            alt=""
          />
        </div>
        <!-- <div v-show="item.open" :class="['slide']"> -->
        <draggable
          v-show="item.open"
          :class="['slide']"
          v-model="componentList[index].list"
          v-bind="dragOptions"
          :options="{
            sort: false,
            group: { name: 'field', pull: 'clone', put: false },
          }"
        >
          <div
            :class="[
              'slide-name',
              selectCom.componentName == it.componentName ? 'item-active' : '',
            ]"
            v-for="(it, ind) in item.list"
            :key="ind"
            @click="itemTap(it, ind)"
          >
            <template v-if="it.componentName">
              <component
                :is="it.componentName"
                :id="it.componentName"
              ></component>
            </template>
          </div>
        </draggable>
      </div>
    </div>

    <div class="content-view">
      <!-- <div class="show-view">
        <p class="show-title">展示区</p>
        <draggable
          group="field"
          v-bind="dragOptions"
          :list="selectList"
          class="show-area"
          @change="changeHandle"
        >
          <div 
            :class="['show-component',index === selectInd ? 'show-active' : '']" 
            v-for="(item, index) in selectList"
            :key="index"
            @click.stop="itemSelect(item,index)"
          >
            {{ item.hiddenLabel ? '': item.name }}
            <room :contentView="item.componentName" :isComponents="true" :options="item.options"></room>
          </div>
        </draggable>
          <input
            type="file"
            id="selectfile"
            @change="importForm()"
          />
      </div> -->
      <div class="code-view">
        <!-- <div :style="{ display: selectCom.componentName ? '' : 'none' }">
            <codeeditor
              ref="codeeditor"
              :formData="formData"
              :h="codeh"
            ></codeeditor>
          </div> -->
        <codeView :value="curCode" @change="change"></codeView>
      </div>
    </div>

    <!-- <div class="attr-view">
      <h6>属性</h6>
    </div> -->
  </div>
</template>
<script>
import lodash from "lodash";
import draggable from 'vuedraggable';
import codeMixin from './mixin.js';
import codeView from "@/views/homepage/components/codeview.vue";

export default {
  mixins: [codeMixin],
  components: {
    draggable,
    codeView,
    // codeeditor,
  },
  data() {
    return {
      lodash,
      dragOptions: {
        animation: 500,
      },
      
      selectList: [],
      selectInd: -1,
      selectCom: {},

      
      formData: { html: "", js: "", css: "" },

      codeh: 800,
      curCode: '<template></template>',
    };
  },
  mounted() {
    const h =
      document.documentElement.clientHeight || document.body.clientHeight;
    this.codeh = h; //- 60
  },
  methods: {
    changeHandle() {
      console.log(this.selectList);
    },

    itemSelect(item, index) {
      this.selectInd = index;
      this.selectCom = item;
    },

    arrowTap(index) {
      const temp = this.componentList;
      temp[index].open = !temp[index].open;
      this.componentList = temp;
    },

    itemTap(it) {
      this.selectCom = it;
      console.log(it);
      const str = `${it.componentName}Code`
      this.curCode = this[str] || '<template></template>'
    },

    codeCopy() {
      var val = document.getElementById("codeBox");
      window.getSelection().selectAllChildren(val);
      document.execCommand("Copy");
      this.$message.success("已复制");
    },

    change(value) {
      console.log(999,value)
      this.curCode = value;
    },
  },
};
</script>
<style scoped>
.homepage {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: row;
}
.slide-view {
  /* flex: 5; */
  width: 700px;
  overflow: auto;
  border-right: 1px solid #999;
  padding:50px 0;
}
.slide-view .item-head {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  font-weight: 600;
  border-bottom: 1px dashed #999;
}
.slide-view .item-head img {
  width: 20px;
  height: 20px;
  margin-left: 15px;
  transition: 0.15s ease-out;
}
.transform180 {
  transform: rotate(180deg);
}
.slide-view .slide {
  width: 100%;
  padding: 0 5px;
  display: flex;
  justify-content: space-around;
  /* justify-content: flex-start; */
  flex-wrap: wrap;
}
.slide-name {
  margin-top: 15px;
  cursor: pointer;
  border: 2px dashed transparent;
}
.slide .item-active {
  border-color: #409eff;
}

/* content-view */
.content-view {
  flex: 8;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #999;
}
.show-view {
  width: 100%;
  /* height: 40%; */
}
input[type="file"] {
  width: 100%;
  height: 100%;
  border: 1px #ff0000;
}

.code-view {
  width: 100%;
  /* height: 60%; */
  height: 100%;
}

/* attr */
.attr-view {
  flex: 2;
}
.attr-view h6 {
  line-height: 60px;
  border-bottom: 1px solid #999;
}
</style>
