<template>
  <div class="code-view">
    <el-button class="code-copy" @click.stop="codeCopy">Copy All</el-button>
    <!-- <el-button class="code-preview" @click.stop="preview">预览</el-button> -->
    <prism-editor
      id="codeBox"
      class="my-editor"
      :value="value"
      @input="codeChange"
      :highlight="highlighter"
      line-numbers
      readonly
      language="vue"
    ></prism-editor>

    <el-drawer
      :with-header="true"
      :visible.sync="previewDrawer"
      direction="rtl"
      size="60%">
        <template slot="title">
          <div style="border: 1px solid;width: 100%;height:100%;">
            <el-button class="code-preview" @click.stop="codeCopy">Copy All</el-button>
          </div>
        </template>
        <prism-editor
          id="codeBox"
          class="my-editor"
          :value="value"
          @input="codeChange"
          :highlight="highlighter"
          line-numbers
          readonly
          language="vue"
      ></prism-editor>
      <!-- <preview-comp v-if="value" :value="value"></preview-comp> -->
    </el-drawer>
  </div>
</template>

<script>
// import Prism Editor
import previewComp from '@/views/homepage/components/cmp.vue';
import { PrismEditor } from "vue-prism-editor";
import "vue-prism-editor/dist/prismeditor.min.css"; // import the styles somewhere

// import highlighting library (you can use any library you want just return html string)
import { highlight, languages } from "prismjs/components/prism-core";
import "prismjs/components/prism-clike";
import "prismjs/components/prism-javascript";
import "prismjs/themes/prism-tomorrow.css"; // import syntax highlighting styles

export default {
  props: {
    value: {
      type: String,
      default: "",
    },
  },
  components: {
    PrismEditor,
    previewComp,
    // previewComp: {
    //         props: {
    //             html: String
    //         },
    //         render(h) {
    //             const com = Vue.extend({
    //                 template: this.html// this.html.replace(/(.?<|\<\/)template>/gi,'')//this.html
    //             })
    //             return h(com, {})
    //         }

    //   // props: {
    //   //   html: {
    //   //     type: String,
    //   //     default: "",
    //   //   },
    //   // },
    //   // render(h) {
    //   //   const com = Vue.extend({
    //   //     template: this.html.replace(/(.?<|\<\/)template>/gi,'')
    //   //   });
    //   //   return h(com, {});
    //   // }
    // },
  },
  data(){
    return {
      newCode:'<template></template>',
      previewDrawer: false,
      codeviewDrawer: false,
    }
  },
  methods: {
    highlighter(value) {
      return highlight(value, languages.js); // languages.js languages.<insert language> to return html with markup
    },
    codeChange(value) {
      // this.$emit("change", value);
      // console.log(666,value)
    },
    codeCopy(e) {
      let val = document.querySelector("#codeBox .prism-editor__editor");
      window.getSelection().selectAllChildren(val);
      document.execCommand("Copy");
      this.$message.success("已复制");
    },

    preview() {
      this.previewDrawer = true;
    },
  },
};
</script>

<style lang="scss">
.code-view {
  position: relative;
  height: 100%;
  
  .my-editor {
    /* we dont use `language-` classes anymore so thats why we need to add background and text color manually */
    background: #2d2d2d;
    color: #ccc;

    /* you must provide font-family font-size line-height. Example: */
    font-family: Fira code, Fira Mono, Consolas, Menlo, Courier, monospace;
    font-size: 14px;
    line-height: 1.5;
    padding: 5px;
  }

  /* optional class for removing the outline */
  .prism-editor__textarea:focus {
    outline: none;
  }

  .code-preview {
    z-index: 1;
    position: absolute;
    top: 0;
    right: 108px;
    font-size: 14px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    min-width: 90px;
    height: 37px;
  }

  .code-copy {
    z-index: 1;
    position: absolute;
    top: 0;
    right: 18px;
    border: none;
    background-color: #000000;
    font-size: 14px;
    color: #ffffff;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    min-width: 90px;
    height: 37px;
  }
  .code-copy:hover {
    background-color: #000000;
    color: #ddd;
    opacity: 0.95;
  }
  .code-copy:active {
    background-color: #252525;
    opacity: 0.95;
  }
}
</style>
